.contact {
    width: 100%;
    height: 90vh;
    display: flex;
    background-color:white;
    flex-direction: row;
}


.contact .leftSide {
    height: 100%;
    flex: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
@media only screen and (max-width: 600px){
    .contact .leftSide {
        display: none;
    }
    form label {
        margin-top: 15px;   
    }
}

.contact .rightSide {
    height: 100%;
    display: flex;
    flex: 50%;
    flex-direction: column;
    justify-content: center;
}

.contact .rightSide h1 {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 10;
    font-size: 50px;
    margin-left: 30px;
    color: rgb(187, 129, 129);
}

form {
    display: flex;
    width: auto;
    height: auto;
    flex-direction: column;
    padding: 30px;
}

form input {
    height: 40px;
    width: 80%;
    font-size: 20px;
    border: none;
    border-bottom: 1px solid black;
    color: rgb(187, 129, 129);
}

form textarea {
    margin-top: 15px;
    height: 70px;
    width: 80%;
    font-size: 20px;
    border: none;
    border-bottom: 1px solid  black;
    color: rgb(187, 129, 129);
}

input::placeholder,
textarea::placeholder{
    font-weight: bold;
    color:rgb(187, 129, 129);
    opacity: .3;
}

form input:focus,
textarea:focus {
    outline: none;
}

form label {
    margin-top: 10px;
    color:rgb(187, 129, 129);
    font-weight: bold;
}

form button {
    margin-top: 40px;
    width: 140px;
    height: 40px;
    border: none;
    background-color:rgb(146, 130, 130);
    color: white;
    font-size: 15px;
    cursor: pointer;
    border-radius: 10px;
}
form button:hover {
    background-color: rgb(187, 129, 129);
}