.navbar {
    width: 100%;
    height: 100px;
    display: flex;
    background-color: #161718;
    flex-direction: row;
    border-bottom: white 300px;
    position: relative;
}

.navbar .leftSide{
    flex: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 150px;
}
.navbar .leftSide img {
 width: 70px;  
 border-radius: 50%; 
}

.navbar .rightSide {
    display: flex;
    flex: 50%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.navbar a {
    color: rgb(187, 129, 129);
    text-decoration: none;
    margin: 20px;
}

.navbar .rightSide button {
    background-color: transparent;
    border: none ;
    color: rgb(187, 129, 129);
    cursor: pointer;
}

.navbar .rightSide svg {
    font-size: 40px;
}

.navbar #open {
    padding-left: 0;
}
.navbar #open img {
    display: none;
}

.navbar #close img {
    display: inherit;
}
.navbar #open .hiddenLinks {
    display: inherit;
    margin-left: 30px;
}

.navbar #close .hiddenLinks{
    display: none;
}

.navbar #open a {
    width:70px ;
    margin: 5px;
}
.navbar #close a {
    display: none;
}

@media only screen and (max-width: 900px ) {
    .navbar .rightSide a {
        width: 70px;
    }
    .navbar .leftSide {
        padding-left: 50px;
    }
    

}

@media only screen and (max-width: 600px){
    .navbar .rightSide a {
        display: none ;
    }
    .navbar .rightSide {
        justify-content: flex-end;
        padding-right: 50px;
    }
    .navbar .rightSide button {
        display: inherit;
    }
}
@media only screen and (min-width:600px){
    .navbar .rightSide button {
        display: none;
    }
    /* .hiddenLinks {
        display: none;
    } */
}