.about {
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
    background-color: white;
}

.aboutTop {
    width: 100%;
    height: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.aboutBottom {
    display: flex;
    flex-direction: column;
    height: 55%;
    align-items: center;
    margin-top: 50px;
}

.aboutBottom p {
    width: 1200px;
    font-size: 1.5rem;
    padding: 10px;
    margin-bottom: auto;
    overflow-wrap: break-word;
    color: rgb(80, 64, 64);
}
@media  only screen and (max-width: 600px) {
    .aboutBottom p {
        width: 400px;
        overflow-wrap: break-word;
        font-size: 1rem;
    }
}

.about .aboutBottom h1 {
    font-weight: 400;
    font-size: 70px;
    color: rgb(187, 129, 129);
    height: 30px;
    display: flex;
    justify-content: center;
    transform: translateY(-40px);
}