.home {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: right;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: "Gil Sans" , "Gill Sans MT , Calibri" , "Trebuchet MS" , sans-serif;
    position: relative;
}


.headerContainer{
    width: auto;
    margin-left: 50px;
}
.headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 50px;
    font-weight: 50px;
    height: 45px;
    z-index: 2;
    color: rgb(187, 129, 129);
    opacity: 1;
}
.headerContainer p {
    padding:0;
    font-size: 33px;
    font-weight: lighter;
    color:rgb(187, 129, 129); ;
    z-index: 5;
}
.headerContainer button {
    color: black;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: #121619;
    height: 40px;
    width: 150px;
    color: white;
    text-decoration: none;
    font-size: 20px;
    z-index: 5;
}

.headerContainer button:hover {
    background-color:rgb(187, 129, 129);
    cursor: pointer;
    transition: 0.3 ease-in;
}
@media  only screen and (max-width: 650px){
    .home{
        justify-content: center;
        align-items: center;
    }

    .headerContainer {
        margin-left: 0;
        border-radius: 10px;
        padding: 10px;
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color:white;
    }
    .headerContainer h1 {
        font-size: 35px;
        font-weight: 30px;
        color: rgb(187, 129, 129);
    }

    .headerContainer p {
        font-size: 30px;
        color: rgb(187, 129, 129);
    }

    .headerContainer button {
        background-color: white;
        color: rgb(187, 129, 129);
        margin-bottom: 30px;
    }

    .headerContainer button:hover{
        background-color:rgb(187, 129, 129);
        color: #121619;
    }
}
